.app {
  position: fixed;
  width: 100%;
  height: 100%;
}

.text-transform-none {
  text-transform: none;
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms;
}

.fade-leave {
  opacity: 1;
}

.fade-leave-active {
  opacity: 0;
  transition: opacity 1000ms;
}

.control-panel {
  position: fixed;
  right: 0.5em;
  bottom: 0.5em;
}

.control-panel-btn {
  animation: unset;
}

.control-panel-btn:hover {
  color: #ffffff !important;
  text-shadow: 1px 1px 1px #ffee58, -1px 1px 1px #ffee58, -1px -1px 1px #ffee58,
    1px -1px 1px #ffee58;
}
