.splash-screen {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  justify-content: center;
  align-items: center;
}

@keyframes transition {
  0% {
    color: #ffffff;
  }

  100% {
    color: #000000;
  }
}

.name {
  animation-name: transition;
  animation-duration: 2s;
  animation-delay: 0s;
  color: #000000;
}
