body {
  font-family: 'Ubuntu', sans-serif;
  color: #212121;
}

a {
  color: #f9a825;
}

a:hover {
  text-decoration: none;
  color: #fbc02d;
  text-shadow: 1px 1px 1px #fdd835, -1px 1px 1px #fdd835, -1px -1px 1px #fdd835,
    1px -1px 1px #fdd835;
}

h5 {
  color: #616161;
}

h6 {
  color: #424242;
}
