.vin-cortez {
  animation: unset;
  position: fixed;
  width: 100%;
  font-size: 1.4em;
  padding-top: 0.4em;
  padding-bottom: 0.4em;
  padding-left: 0.8em;
}

.vin-cortez:hover {
  color: #ffffff !important;
  text-shadow: 1px 1px 1px #ffee58, -1px 1px 1px #ffee58, -1px -1px 1px #ffee58,
    1px -1px 1px #ffee58;
}
